.form {
  input {
    font-family: $font-default;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    border: none;
    outline: 0;
    background-color: transparent;
  }

  input::placeholder {
    font-family: $font-default;
    font-weight: 500;
    font-size: 14px;
    line-height: 21px;
    color: $gray;
  }

  &--search {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    width: 184px;
    margin-top: 14px;

    label {
      position: relative;
      display: flex;
      align-items: flex-start;
      padding: 0;
    }

    input {
      height: 14px;
    }

    svg {
      margin-left: 5px;
      margin-right: 13px;
      fill: $black;
    }

    @media (max-width: $tablet-width) {
      display: none;
      width: 100%;
      grid-column: 1/4;
      grid-row: 2/3;
      margin-top: 32px;

      label {
        width: 100%;
      }

      input {
        display: flex;
        padding-left: 40px;
        width: 100%;
        height: 36px;
        background-color: rgba(255, 255, 255, 0.15);
        border-radius: 21px;
        color: $white;
      }

      input::placeholder {
        color: transparent;
      }

      svg {
        position: absolute;
        left: 9px;
        top: 12px;
        fill: $white;
      }
    }
  }

  &--footer {
    input {
      padding-left: 0;
      color: $white;
    }

    button {
      padding: 0;
      font-family: $font-default;
      font-weight: 500;
      font-size: 16px;
      line-height: 21px;
      border: 0;
      color: $white;
      background-color: transparent;
      cursor: pointer;
    }

    button:hover {
      color: $accent;
      transition: 0.3s;
    }

    button:hover svg {
      stroke: $accent;
      transition: 0.3s;
    }

    svg {
      stroke: $white;
      margin-left: 8px;
    }

    @media (max-width: $tablet-width) {
      max-width: 559px;
    }
  }

  &--login {
    input {
      width: 100%;
      padding-left: 0;
      border-bottom: 1px solid $black;
    }

    input[type="email"] {
      margin-bottom: 34px;
      padding-bottom: 8px;
    }

    input[type="password"] {
      margin-bottom: 5px;
      padding-bottom: 8px;
    }

    a {
      display: block;
      position: relative;
      text-align: right;
      margin-left: auto;
      font-weight: 500;
      font-size: 12px;
      line-height: 18px;
      color: $gray;
    }

    a:hover {
      color: $accent;
      transition: 0.3s;
    }

    button {
      display: inline-block;
      margin-top: 45px;
      width: 100%;
      min-height: 68px;
      vertical-align: middle;
      font-family: $font-default;
      font-weight: 500;
      font-size: 16px;
      line-height: 130%;
      color: $white;
      background-color: $accent;
      border: 0;
      cursor: pointer;
    }

    button:hover {
      color: transparentize($white, 0.5);
      transition: 0.3s;
    }

    @media (max-width: $mobile-width) {
      input[type="email"] {
        margin-bottom: 29px;
      }

      button {
        margin-top: 35px;
      }
    }
  }
}
